.about {
    flex-direction: column;
    margin-top: 1em;
    font-size: 2rem;
  }
  .about__name {
    color: var(--clr-primary);
  }
  
  .about_desc{
    margin:auto;
    padding:20px;
    margin-bottom:50px;
    margin-top: 2.4em;
    display: flex;
    gap: 20px;
    font-size: 1.1em;
  }
  .about_contact {
    margin-top: 2.4em;
    display: flex;
    gap: 20px;
  }
  
  .about .link--icon {
    margin-right: 0.8em;

  }
  
  .about .btn--outline {
    margin-right: 1em;
  }
  
  @media (max-width: 600px) {
    .app .about {
      /* align-items: flex-start; */
      margin-top: 2em;
      justify-content: center;
      align-items: center;
    }
    .about > :nth-child(2) {
      font-size: 1.5rem;
    }
  }
  